import React, { useState, useEffect } from 'react'
import { useSpring, animated } from 'react-spring'
// import ReactDOM from 'react-dom';
import VertexDisplacementCore from '../components/VertexDisplacementCore'
import '../css/Header.css'

const calc = (x, y) => [x - window.innerWidth / 2, y - window.innerHeight / 2]
const trans1 = (x, y) => `translate3d(${x / 15}px,${y / 15}px,0)`
const trans2 = (x, y) => `translate3d(${x / 8}px,${y / 8}px,0)`
const trans3 = (x, y) => `translate3d(${x / 6}px,${y / 6}px,0)`
const trans4 = (x, y) => `translate3d(${x / 3.5}px,${y / 3.5}px,0)`

const Header = () => {

    const [props, set] = useSpring(() => ({ xy: [0, 0], config: { mass: 10, tension: 550, friction: 140 } }))

    const randColour = `hsl(${Math.floor(Math.random() * 360) + 1}, 74%, 57%)`
    console.log(randColour)

    return (
        <div className='wrapper'>
            <div className='parallax-core' onMouseMove={({ clientX: x, clientY: y }) => set({ xy: calc(x, y) })} />
            <div className='bg' >
                <VertexDisplacementCore renderWidth={window.innerWidth-25} renderHeight={window.innerHeight-25} size={15} />
            </div>
            <div className='navbar-container'>
                <div className='navbar'>
                    <a href='/' className='left'>Folio 2021</a>
                    <a href='#programming' className='right'>Programming</a>
                    <a href='#graphics' className='right'>Graphics</a>
                    <a href='#about' className='right'>About</a>
                </div>
            </div>
            <div className='title-container' >
                <animated.h1 className='title' style={{ transform: props.xy.to(trans1) }}>Paralax</animated.h1>
            </div>
            {/* <div className='bg-colour' style={{backgroundColor: randColour, height: '100vh', width: '100vw', position: 'absolute', zIndex: '-10000', top: '0px'}} /> */}
        </div>
    )
}

export default Header
